a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  font-family: "Ubuntu", "Arial", sans-serif;
}

br {
  @media (min-width: @tablet-width) {
    display: none;
  }
}

body {
  font-weight: 400;
  color: #292929;
  // word-wrap: break-word;
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
